body {
  height: 100vh;
  background-image: url("bg-feedback-form.8f1655b9.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.header, .section__feedback {
  padding-top: 20px;
}

.link-arrow-left {
  color: #3a4d39;
  margin-left: 80%;
  display: inline-block;
}

@media (min-width: 768px) {
  .link-arrow-left {
    margin-left: 80%;
  }
}

@media (min-width: 1536px) {
  .link-arrow-left {
    margin-left: 27%;
  }
}

.feedback-form {
  max-width: 480px;
  backdrop-filter: blur(10px);
  background: rgba(115, 144, 114, .5);
  border: 1px solid #739072;
  border-radius: 2px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

@media (min-width: 768px) {
  .feedback-form {
    margin: 0 auto;
  }
}

@media (min-width: 1536px) {
  .feedback-form {
    max-width: 660px;
    padding: 20px 30px;
  }
}

.feedback-form label {
  text-align: center;
  color: #3a4d39;
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  color: #ece3ce;
  backdrop-filter: blur(10px);
  font: inherit;
  cursor: pointer;
  background-color: #4f6f52;
  border: 1px solid #3a4d39;
  border-radius: 2px;
  padding: 8px 12px;
}

input, textarea {
  color: #3a4d39;
  font: inherit;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #3a4d39;
  border-radius: 2px;
  outline-color: #4f6f52;
  margin-top: 5px;
  padding: 8px;
}

textarea {
  resize: none;
}

.title {
  color: #3a4d39;
  text-align: center;
  border-bottom: 1px solid #4f6f52;
  margin: 0 0 10px;
  padding: 10px 0;
}

